import React, { Component } from 'react';
import ServiceTabs from './ServiceTabs.js';
import LocationTabs from './LocationTabs.js';
import ContactBar from './ContactBar.js';

import HeaderBar from './HeaderBar.js';
import MobileHeaderBar from './MobileHeaderBar.js';
import InfoTile from './InfoTile.js';
import StepMenu from './StepMenu.js';
import Media from 'react-media';
import './App.css';
import "animate.css/animate.min.css";


class App extends Component {

constructor(props){
  super(props);

  this.BodyContainer = this.BodyContainer.bind(this);
}

  BodyContainer(){

    return(

    <div className="Body-Container">



      {/** Home Tile **/}

      <div className="home-tile">
        <div className="home-title">Welcome To<br />Apex Downriver Behavioral Health</div>

        Provider of Outpatient Behavioral Health and Specialized Behavioral Health Services
        <br />
      </div>

      <div id="forms-menu"> <StepMenu /> </div>

      {/** About Us Tile **/}

        <InfoTile
          title="About Us"
          tileName="AboutUs"
          bgColor="var(--color-6)"
          fontColor="var(--color-1)">

          <strong>One Size Does Not Fit All:</strong> <br /> <br />
            Each of us is unique and each of us experience mental health and substance abuse problems in a unique way.
            Our job at Apex Downriver is to develop with you a plan of treatment that is specific to your needs.
            <br /> <br />
            For one person talking with a psychologist, social worker or counselor is perfect.
            For another person talk therapy is not beneficial but medication treatment from one of our psychiatrists is.
            Another person may find the combination of  psychotherapy and medication is the perfect fit.
            <br /> <br />
            Based on your background and needs we will develop a treatment plan for you that addresses your personal concerns.
            We will make recommendations but ultimately you can be the one to determine what works best for you!

        </InfoTile>

      {/** Services Tile **/}

        <InfoTile
          title="Services"
          tileName="Services"
          bgColor="var(--color-1)"
          fontColor="var(--color-6)">

          <ServiceTabs />

        </InfoTile>

    {/** Contact / Location Tile **/}

        <InfoTile
          title="Contact Us"
          tileName="ContactUs"
          bgColor="var(--color-2)"
          fontColor="var(--color-6)">

        <LocationTabs />

        </InfoTile>

        <ContactBar />

    </div>

  );
  }


  render() {
    return (

      <div className="App" id="Top">

          <Media query="(max-width: 799px)">
            {matches =>
              matches ? (
              <div>
                <MobileHeaderBar />
                {this.BodyContainer()}
              </div>
              ) : (
              <div>
                <HeaderBar />
                <div style={{height: '100px'}} />
                {this.BodyContainer()}
              </div>
              )
            }
          </Media>

      </div>
    );
  }
}

export default App;



