import React, {Component} from 'react';
import {HashLink as Link} from 'react-router-hash-link';
import SVG from 'react-inlinesvg';
import upArrow from './images/arrow-up.svg'
import Logo from './images/logo.jpg';

class ContactBar extends Component {

	constructor(props){
		super(props);

		this.state={
			credits: false
		}

		this.showCredits = this.showCredits.bind(this);
		this.toggleCredits = this.toggleCredits.bind(this);
	}

	toggleCredits(){

		let showThemCredits = !this.state.credits;

		this.setState(prevState => ({
			credits: showThemCredits
		}));

		this.showCredits();

	}

	showCredits = () => {
		console.log(this.state.credits);

		if(this.state.credits === true){
			return(
				<div className='credits-text' onClick={(e) => this.toggleCredits()}>
					Icons courtesy of <a href="https://fontawesome.com/license" target="_blank" rel="noopener noreferrer" className="bottom-link">Font Awesome</a>
					<br />
					Design by <a href="https://ca7777.github.io" target="_blank" rel="noopener noreferrer" className="bottom-link">Charles Adams</a>
				</div>
			)} else if(this.state.credits === false){
				return(
					<div className='credits-text' onClick={(e) => this.toggleCredits()}>Apex Downriver Behavioral Health, 2018</div>
				)
			}

	}

	render(){

		return (

			<div className="contact-bar">
					<img src={Logo} alt="Apex Downriver Behavioral Health" className="bottom-logo" />

					<div className="button-to-top">	<Link to="/#Home" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>

		          <SVG
		            src={upArrow}
		            className="link-icon"
		           /><br />
							 Back To Top

						</Link></div>

					{this.showCredits()}

			</div>

		)

	}

}

export default ContactBar



