import React, { Component } from "react";
import SVG from "react-inlinesvg";
import {HashLink as Link} from 'react-router-hash-link';
import logo from "./images/logo.jpg";
import homeImg from "./images/home-solid.svg";
import aboutImg from "./images/users-solid.svg";
import servicesImg from "./images/cogs-solid.svg";
import contactImg from "./images/envelope-solid.svg";

const scrollWithOffset = (el, offset) => {

	const elementPosition = el.offsetTop - offset;
	window.scrollTo({
		top: elementPosition,
		left: 0,
		behavior: "smooth"
	});
};

class HeaderBar extends Component {


render(){

	return(
	<div id='Header-Bar'>

    <div id="logo-container">
      <img src={logo} className="app-logo" alt="Apex Downriver Behavioral Health" />
    </div>

    <div className="LinkBar">
				<Link to="#Top" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			   <div id="HomeLink" className="LinkItem">
          <SVG
            src={homeImg}
            className="link-icon"
           />
            <br />
  				  Home
  			</div>
				</Link>

			<Link to="#AboutUs" scroll={el => scrollWithOffset(el, 150)}>
  			<div id="AboutLink" className="LinkItem">
          <SVG
            src={aboutImg}
            className="link-icon"
           />
            <br />
  				About Us
  			</div>
			</Link>

		 <Link to="#Services" scroll={el => scrollWithOffset(el, 150)}>
  			<div id="ServicesLink" className="LinkItem">

	        	<SVG
	          	src={servicesImg}
	          	className="link-icon"
	         	/>
	          <br />
	  				Services
				</div>
			</Link>

			<Link to="#ContactUs" scroll={el => scrollWithOffset(el, 150)}>
        <div id="ContactLink" className="LinkItem">
        <SVG
          src={contactImg}
          className="link-icon"
         />
          <br />

  				Contact Us
  			</div>
			</Link>

			</div>



	</div>
  )
  };

};


export default HeaderBar;



