import React, {Component} from 'react';
import { TabProvider, Tab, TabPanel, TabList } from 'react-web-tabs';
import SVG from "react-inlinesvg";
import Media from 'react-media';

import adultIcon from './images/male-solid.svg';
import childIcon from './images/child-solid.svg';
import helpIcon from './images/plus-circle-solid.svg';
import carIcon from './images/car-solid.svg';
import gavelIcon from './images/gavel-solid.svg';

import 'react-web-tabs/dist/react-web-tabs.min.css';

class ServiceTabs extends Component {

  render(){

    return(
<Media query="(min-width: 799px)">
  {matches =>
    matches ? (
      <TabProvider defaultTab="one" vertical className="my-table">
        <section className="my-tabs">
          <TabList className="my-tablist" >
            <Tab tabFor="one" className="my-tab">
              <SVG
                src={adultIcon}
                className="link-icon"
               />
             </Tab>

            <Tab tabFor="two" className="my-tab">
              <SVG
                src={childIcon}
                className="link-icon"
               />
             </Tab>

            <Tab tabFor="three" className="my-tab">
              <SVG
                src={helpIcon}
                className="link-icon"
               />
             </Tab>

             <Tab tabFor="four" className="my-tab">
               <SVG
                 src={carIcon}
                 className="link-icon"
                />
              </Tab>

              <Tab tabFor="five" className="my-tab">
                <SVG
                  src={gavelIcon}
                  className="link-icon"
                 />
               </Tab>
          </TabList>

            <TabPanel tabId="one" className="my-panel">

              <div className="panel-title">Mental Health Services for Adults</div>
              <div className='panel-content'>
              Adults frequently come to us for treatment of their depression or anxiety. Clients frequently come to us for feelings of helplessness, hopelessness, not being able to slow down our thoughts or feelings that we have stopped caring. We also see adult clients who have a physical illness or injury that they increasingly feel they can not cope. We also see clients who are married or in a relationship with an individual who has a chronic mental health or physical illness and find that they can no longer cope with their spouse and his or her illness. Additionally, many individuals seek help from us with they have experience a significant loss or trauma in his or her life, ie death of someone close to them, divorce, losing a job among the other life stressors we sometimes have to deal with. While at the time of intake you will be asked whether you wish to receive services from a psychiatrist or a therapist, at your first assessment we will evaluate and recommend what would best meet your needs. We are an interdisciplinary team of psychiatrists, psychologists, social workers, and counselors and can develop with you a treatment plan that best meets your needs.
              </div>

            </TabPanel>

            <TabPanel tabId="two" className="my-panel">

                <div className="panel-title">Mental Health Services for Children and Teenagers</div>
                <div className="panel-content">
                We understand that in most instances it will be the parent who calls and makes an appointment for their child or teenager. We always wish the parent to be involved in the child or teenagers treatment. We also understand the complexities that come with a child or teenager receiving therapy in a divorce situation. We will strive to keep both parents involved in their child's care within orders of the divorce. Also, we understand that today's families can be complicated and it may be a grandparent or other relative who brings the child or teenager for therapy. We will strive to obtain permission from the parents to keep involved in the child's or teenager's therapy the individuals the parents want involved. With the parent's permission, we will also communicate with the child or teen's school and coordinate our care with their school experience.
                </div>

            </TabPanel>

            <TabPanel tabId="three" className="my-panel">

            <div className="panel-title">  Alcohol and substance abuse problems </div>
            <div className="panel-content">
              As with mental health, we understand that individuals experience alcohol and substance abuse problems in different ways. We offer traditional one to one therapy outpatient therapy. We also offer an intensive outpatient program that meets three times per week, three hours per session for those that need more to stay sober. We also offer a group therapy program on a once per week basis for individual who need more than outpatient but less than intensive outpatient. This program called Sojourn is also specifically developed for individuals who are court ordered for substance abuse evaluation and treatment.
            </div>


            </TabPanel>

            <TabPanel tabId="four" className="my-panel">

            <div className="panel-title">  Driver's License Assistance </div>

            <div className="panel-content">  We also offer evaluation for those individuals who have lost their driver's license but meet the State criteria to have his driver license restored. We can walk you through the process and for those who have a commitment to stay sober and comply with the State requirements in order to get his driver license restored. </div>


            </TabPanel>

            <TabPanel tabId="five" className="my-panel">

            <div className="panel-title"> Corrections Programs </div>

            <div className="panel-content"> For over 20 years we have provided substance abuse and mental health services for inmates, parolees, and probationers. We provide these services both in the community and in prisons. To qualify for these services individuals must be referred by designated staff within the Michigan Department of Corrections </div>

            </TabPanel>

        </section>
      </TabProvider>
  ) : (
    <TabProvider defaultTab="one" className="my-table">
      <section className="my-tabs-mobile">
        <TabList className="my-tablist-mobile" >
          <Tab tabFor="one" className="my-tab-mobile">
            <SVG
              src={adultIcon}
              className="link-icon"
             />
           </Tab>

          <Tab tabFor="two" className="my-tab-mobile">
            <SVG
              src={childIcon}
              className="link-icon"
             />
           </Tab>

          <Tab tabFor="three" className="my-tab-mobile">
            <SVG
              src={helpIcon}
              className="link-icon"
             />
           </Tab>

           <Tab tabFor="four" className="my-tab-mobile">
             <SVG
               src={carIcon}
               className="link-icon"
              />
            </Tab>

            <Tab tabFor="five" className="my-tab-mobile">
              <SVG
                src={gavelIcon}
                className="link-icon"
               />
             </Tab>
        </TabList>

          <TabPanel tabId="one" className="my-panel">

            <div className="panel-title">Mental Health Services for Adults</div>
            <div className='panel-content'>
            Adults frequently come to us for treatment of their depression or anxiety. Clients frequently come to us for feelings of helplessness, hopelessness, not being able to slow down our thoughts or feelings that we have stopped caring. We also see adult clients who have a physical illness or injury that they increasingly feel they can not cope. We also see clients who are married or in a relationship with an individual who has a chronic mental health or physical illness and find that they can no longer cope with their spouse and his or her illness. Additionally, many individuals seek help from us with they have experience a significant loss or trauma in his or her life, ie death of someone close to them, divorce, losing a job among the other life stressors we sometimes have to deal with. While at the time of intake you will be asked whether you wish to receive services from a psychiatrist or a therapist, at your first assessment we will evaluate and recommend what would best meet your needs. We are an interdisciplinary team of psychiatrists, psychologists, social workers, and counselors and can develop with you a treatment plan that best meets your needs.
            </div>

          </TabPanel>

          <TabPanel tabId="two" className="my-panel">

              <div className="panel-title">Mental Health Services for Children and Teenagers</div>
              <div className="panel-content">
              We understand that in most instances it will be the parent who calls and makes an appointment for their child or teenager. We always wish the parent to be involved in the child or teenagers treatment. We also understand the complexities that come with a child or teenager receiving therapy in a divorce situation. We will strive to keep both parents involved in their child's care within orders of the divorce. Also, we understand that today's families can be complicated and it may be a grandparent or other relative who brings the child or teenager for therapy. We will strive to obtain permission from the parents to keep involved in the child's or teenager's therapy the individuals the parents want involved. With the parent's permission, we will also communicate with the child or teen's school and coordinate our care with their school experience.
              </div>

          </TabPanel>

          <TabPanel tabId="three" className="my-panel">

          <div className="panel-title">  Alcohol and substance abuse problems </div>
          <div className="panel-content">
            As with mental health, we understand that individuals experience alcohol and substance abuse problems in different ways. We offer traditional one to one therapy outpatient therapy. We also offer an intensive outpatient program that meets three times per week, three hours per session for those that need more to stay sober. We also offer a group therapy program on a once per week basis for individual who need more than outpatient but less than intensive outpatient. This program called Sojourn is also specifically developed for individuals who are court ordered for substance abuse evaluation and treatment.
          </div>


          </TabPanel>

          <TabPanel tabId="four" className="my-panel">

          <div className="panel-title">  Driver's License Assistance </div>

          <div className="panel-content">  We also offer evaluation for those individuals who have lost their driver's license but meet the State criteria to have his driver license restored. We can walk you through the process and for those who have a commitment to stay sober and comply with the State requirements in order to get his driver license restored. </div>


          </TabPanel>

          <TabPanel tabId="five" className="my-panel">

          <div className="panel-title"> Corrections Programs </div>

          <div className="panel-content"> For over 20 years we have provided substance abuse and mental health services for inmates, parolees, and probationers. We provide these services both in the community and in prisons. To qualify for these services individuals must be referred by designated staff within the Michigan Department of Corrections </div>

          </TabPanel>

      </section>
    </TabProvider>
      )
    }
    </Media>
    )

  }

};

export default ServiceTabs;



