import React, {Component} from 'react';
import SVG from 'react-inlinesvg';
import ScrollAnimation from 'react-animate-on-scroll';
import Media from 'react-media';

import closeX from './images/close-solid.svg';
import childforms from './docs/child_forms.doc';
import adultforms from './docs/adult_forms.doc';
import childformspdf from './docs/child_forms.pdf';
import adultformspdf from './docs/adult_forms.pdf';

const description = (
    <div className="form-menu-description">To save time in the office before your first visit, please download and print the following forms: </div>
)

const closeBtn = (

  <Media query="(max-width: 799px)">
    {matches =>
      matches ? (
        <SVG
          src={closeX}
          className="x-button-mobile"

        />
      ) : (
        <SVG
          src={closeX}
          className="x-button"

        />
      )
    }
  </Media>

)

class StepMenu extends Component {

  constructor(props){
    super(props);

    this.state=({
      step: 0,
      isMinor: false
    });

    this.formPresent = this.formPresent.bind(this);
    this.startStep = this.startStep.bind(this);
    this.nextStep = this.nextStep.bind(this);
    this.selectForms = this.selectForms.bind(this);
    this.stepSelector = this.stepSelector.bind(this);
    this.startOver = this.startOver.bind(this);
  }

  nextStep(newStep){
      this.setState(prevState => ({
          step: newStep
        }));
      console.log(this.state.step);

  };

  startOver(){
    this.setState(prevState => ({
        step: 0
      }));
    this.stepSelector();
  }

  selectForms(x){
      this.setState(prevState => ({
          isMinor: x
        }));

      this.nextStep(1);
      this.stepSelector();
  };

  stepSelector(){
    if (this.state.step===0){
      return this.startStep();
    } else if (this.state.step===1){
       return this.formPresent();
    }
  }




  formPresent(){


    if (this.state.isMinor){

      return(
        <div className="form-menu step-tile" >
          {description}

          <br />          Format: <br />
          <div className="step-menu-button-box">

            <a href={childforms} target="_blank" className="step-menu-button">Microsoft Word</a>
            <a href={childformspdf} target="_blank" className="step-menu-button">PDF</a>
          </div>

          <div className="form-menu-subtext">
            Please fill these forms out for your child and bring them, along with a photo ID of their parent or guardian and insurance card, to your first visit.
          </div>

          <div onClick={(e) => this.startOver()}>{closeBtn}</div>

        </div>
      );

    } else if (!this.state.isMinor) {
      return(
      <div className="form-menu step-tile">
        {description}

          <br />          Format: <br />
        <div className="step-menu-button-box">

          <a href={adultforms} target="_blank" className="step-menu-button">Microsoft Word</a>
          <a href={adultformspdf} target="_blank" className="step-menu-button">PDF</a>
        </div>

        <div className="form-menu-subtext">
          Please fill these forms out and bring them, along with your photo ID and insurance card, to your first visit.
        </div>

        <div onClick={(e) => this.startOver()}>{closeBtn}</div>

      </div>
    );
    }

  }

  startStep(){


      return(
          <div className="start-step-tile step-tile">

          {description}

          <br />          Select Patient Age: <br />

            <div className="step-menu-button-box">
              <div id="Over18" className="step-menu-button" onClick={(e) => this.selectForms(false)}>Over 18</div>
              <div id="Under18" className="step-menu-button" onClick={(e) => this.selectForms(true)}>Under 18</div>
            </div>
          </div>
        );
  };


  render(){
    return (
      <ScrollAnimation animateIn="slideInLeft" animateOnce={true} viewport={50}> <div style={{backgroundColor: this.props.bgColor,
                                           color: this.props.fontColor}}>
      {this.stepSelector()}
      </div>
      </ScrollAnimation>
    );
  }

}

export default StepMenu;



