import React, { Component } from "react";
import SVG from "react-inlinesvg";
import {HashLink as Link} from 'react-router-hash-link';

import logo from "./images/logo.jpg";
import homeImg from "./images/home-solid.svg";
import aboutImg from "./images/users-solid.svg";
import servicesImg from "./images/cogs-solid.svg";
import contactImg from "./images/envelope-solid.svg";
import menuImg from './images/bars-solid.svg';

class MobileHeaderBar extends Component {


		constructor(props){
			super(props);

			this.state = {
				show: true
			}

			this.toggleMenu = this.toggleMenu.bind(this);
			this.MenuBar = this.MenuBar.bind(this);
		};

		toggleMenu(){

			this.setState(prevState => ({
	      		show: !this.state.show
	    	}));

		}

		MenuBar(){
			if (this.state.show===true) { return (
			<div className="MobileLinkBar">
				<Link to="/#Home" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			 		<div id="HomeLink" className="MobileLinkItem">
				 	<SVG
					 	src={homeImg}
					 	className="mobile-link-icon"
						/>
						<div className="mobile-link-text">Home</div>
					 </div>
				 </Link>

				 <br />
			<Link to="/#AboutUs" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			 <div id="AboutLink" className="MobileLinkItem">
				 <SVG
					 src={aboutImg}
					 className="mobile-link-icon"
					/>
			<div className="mobile-link-text">About Us </div>
			 </div>
			 </Link>
			 <br />
			 <Link to="#Services" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			 <div id="ServicesLink" className="MobileLinkItem">
			 <SVG
				 src={servicesImg}
				 className="mobile-link-icon"
				/>


			<div className="mobile-link-text">Services </div>

			 </div>
			 </Link>
			 <br />
			 <Link to="#Contact" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			 <div id="ContactLink" className="MobileLinkItem">
			 <SVG
				 src={contactImg}
				 className="mobile-link-icon"
				/>

				<div className="mobile-link-text">Contact Us</div>

			 </div>
			 </Link>

		 </div>
	 )} else {
		  return (
 			<div className="MobileLinkBarClosed">

			<div className="MobileLinkBar">
			 <Link to="/#Home" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
				 <div id="HomeLink" className="MobileLinkItem">
				 <SVG
					 src={homeImg}
					 className="mobile-link-icon"
					 />
					 <div className="mobile-link-text">Home</div>
					</div>
				</Link>

				<br />
		 <Link to="/#AboutUs" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			<div id="AboutLink" className="MobileLinkItem">
				<SVG
					src={aboutImg}
					className="mobile-link-icon"
				 />
		 <div className="mobile-link-text"> About Us </div>
			</div>
			</Link>
			<br />
			<Link to="#Services" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			<div id="ServicesLink" className="MobileLinkItem">
			<SVG
				src={servicesImg}
				className="mobile-link-icon"
			 />


		 <div className="mobile-link-text">	 Services </div>

			</div>
			</Link>
			<br />
			<Link to="#Contact" scroll={el => el.scrollIntoView({ behavior: 'smooth', block: 'start'})}>
			<div id="ContactLink" className="MobileLinkItem">
			<SVG
				src={contactImg}
				className="mobile-link-icon"
			 />

			 <div className="mobile-link-text">Contact Us</div>

			</div>
			</Link>

		</div>

 		 </div>
		 )
	 }
	}

	render(){

	return(
		<div id='Mobile-Header-Bar' >
			<header className="Mobile-App-header">

	    <div>
			<img src={logo} className="app-logo" alt="Apex Downriver Behavioral Health"/>
	    </div>

			<div className="menu-button" onClick={(e) => this.toggleMenu() }>
				<SVG
					src={menuImg}
					className="menu-icon"
					/>

			</div>

			{this.MenuBar()}


		</header>

	</div>

	)};
}

export default MobileHeaderBar;



