import React, {Component} from 'react';
import Media from 'react-media';
import ScrollAnimation from 'react-animate-on-scroll';


class InfoTile extends Component {


  render(){



    return(
      <div id={this.props.tileName}>
      <ScrollAnimation animateIn="slideInRight" animateOnce={true} viewport={50}>
      <Media query="(max-width: 1000px)">
        {matches =>
          matches ? (

      <div className="mobile-info-tile" style={{backgroundColor: this.props.bgColor,
                                                                                   color: this.props.fontColor
                                                                                          }}>

        <div className="mobile-title-space" >
          {this.props.title}
        </div>

        <div className="mobile-tile-description">
          {this.props.children}
        </div>

      </div>

      ) : (

        <div className="info-tile" id={this.props.tileName} style={{backgroundColor: this.props.bgColor, color: this.props.fontColor
                                                                                            }}>
          <div className="title-space" >
            {this.props.title}
            <br />
            <div className="title-icon"> </div>
          </div>

          <div className="tile-description">
            {this.props.children}
          </div>
        </div>
      )
    }
    </Media>
    </ScrollAnimation>
    </div>
    );

  };

}

export default InfoTile



